.container {
  background: rgba(255, 255, 255, 0.2);
  border-radius: 1.5em;
  padding-top: 10vh;
  padding-bottom: 10vh;
  -webkit-backdrop-filter: blur(4px);
  backdrop-filter: blur(4px);
  border: 1px solid rgba(255, 255, 255, 0.25);
  box-shadow: 10px 10px 10px rgba(46, 54, 68, 0.08);
}

